import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilDataTransferDown, cilPencil } from '@coreui/icons'
import {
  CRow,
  CCol,
  CButton,
  CFormInput,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModal,
  CImage,
} from '@coreui/react'
import { quotationLists } from '../../mocks/quotationData'
import demo from './../../assets/images/demo.jpg'

const QuotationList = () => {
  const [open, setOpen] = useState(false)
  return (
    <>
      <CRow className="form-box">
        <CCol md={2}>
          <CFormInput label="報價單編號" placeholder="請輸入報價單編號" />
        </CCol>
        <CCol md={2}>
          <CFormInput label="客戶統編" placeholder="請輸入客戶統編" />
        </CCol>
        <CCol md={2}>
          <CFormInput type="date" label="起始日期" placeholder="請輸入日期" />
        </CCol>
        <CCol md={2}>
          <CFormInput type="date" label="結束日期" placeholder="請輸入日期" />
        </CCol>

        <CCol md={2} className="d-flex align-items-end">
          <CButton color="primary">搜尋</CButton>
        </CCol>
      </CRow>
      <CRow className="list-head">
        <CCol md={2}>報價單編號</CCol>
        <CCol md={3}>客戶統編</CCol>
        <CCol md={2}>建立時間</CCol>
        <CCol md={2}>狀態</CCol>
        <CCol md={2}>操作</CCol>
      </CRow>
      <div className="list-body">
        {quotationLists && quotationLists.length
          ? quotationLists.map((item) => {
              return (
                <CRow className="list-item" key={item.num}>
                  <CCol md={2}>{item.num}</CCol>
                  <CCol md={3}>{item.client}</CCol>
                  <CCol md={2}>{item.createDate}</CCol>
                  <CCol md={2}>{item.statusText}</CCol>
                  <CCol md={2}>
                    {item.status === 'A' && (
                      <Link to="/quotation/edit" className="action-icon">
                        <CIcon className="" icon={cilPencil} />
                      </Link>
                    )}
                    {(item.status === 'C' || item.status === 'D') && (
                      <Link to="/pdfContainer" className="action-icon">
                        <CIcon icon={cilDataTransferDown} />
                      </Link>
                    )}
                  </CCol>
                </CRow>
              )
            })
          : null}
      </div>
      <CModal visible={open} onClose={() => setOpen(false)}>
        <CModalHeader>
          <CModalTitle>報價單預覽</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="px-2">
            <CImage fluid src={demo} />
          </div>
        </CModalBody>
      </CModal>
    </>
  )
}

export default QuotationList
